@import "variables";

.movie-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Space Grotesk", sans-serif;
  height: 35vh;
  width: 15vw;
  margin: 7vh 5vw;
  color: $White;
  background-color: $dark-grey;
  text-decoration: none;
}

.movie-img-container {
  transition: cursor 0.3s;
  &:hover {
    cursor: pointer;
  }
}

.poster-img {
  padding: 0.5em;
  max-height: 30vh;
  max-width: 25vw;
  border-radius: 5%;
  transition: transform 0.3s, opacity 0.3s, box-shadow 0.3s;
  &:hover {
    transform: scale(1.05);
    opacity: 65%;
    box-shadow: black;
  }
  &:active {
    transform: scale(1);
  }
}

.rating-star-movie-card,
.watch-list-icon {
  padding-top: 0.2em;
  height: 1.25em;
  width: 1.25em;
  filter: invert(91%) sepia(22%) saturate(6764%) hue-rotate(5deg)
    brightness(101%) contrast(104%);
  transition: cursor 0.3s;
  &:hover {
    cursor: pointer;
  }
}
.watch-list-icon {
  margin: -6.75% 0.1em;
}

.average-rating-container {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
}

.movie-card-title,
.average-rating-text,
.release-date-text {
  margin: 1.25%;
  transition: transform 0.3s;
  &:hover {
    transform: scale(1.25);
  }
}

@media only screen and (max-width: 650px) {
  .movie-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Space Grotesk", sans-serif;
    height: 35vh;
    width: 25vw;
    margin: 5vh 0 45vh;
    color: $White;
    background-color: $dark-grey;
    text-decoration: none;
  }

  .poster-img {
    max-width: 75vw;
    max-height: 50vh;
  }

  .movie-info-container {
    width: 75vw;
  }

  .watch-list-icon {
    margin: -2.75% 0.1em;
  }
}
